import { Component,NgModule, OnInit, ChangeDetectorRef, ChangeDetectionStrategy } from '@angular/core';

@Component({
  selector: 'app-root',
  template: '<router-outlet></router-outlet>',
  styleUrls: ['./app.component.scss']
  // changeDetection: ChangeDetectionStrategy.OnPush
})

export class AppComponent implements OnInit {
    public time: any;
    public normal_title: any = "Hi,小汪";
    public loading: any = {
        display : 'none'
    };

    private timer;

    constructor() {
        this.loading = {
           display : 'block'
        };

        //浏览器title监听事件 
        this.Index();
    }


    ngOnInit() {

    }

    //浏览器title监听事件 
    Index() {
        window.addEventListener('visibilitychange', function () {
            if (document.visibilityState == 'hidden') {
                this.normal_title = document.title;
                document.title = '出BUG了，快回来！';
            } else document.title = "Hi,小汪";
        })
    }


}




